@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.login-page {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.login-image {
  min-width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #6456f7;
}

.login-image img {
  max-height: 50vh;
}

.login-form {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  min-width: 50%;
  height: 100vh;
}

.title {
  display: flex;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  position: absolute;
  top: 10%;
}

.title h1 {
  font-size: 38px;
}

.title img {
  width: 40px;
}

.login-input {
  border: 3px black solid;
  border-radius: 6px;
  font-size: 18px;
  margin: 20px;
}

.login-label {
  font-size: 18px;
}

.button-container {
  margin: 10px;
}

.enabled-button {
  background-color: #16ad88;
  border-radius: 4px;
  font-size: 20px;
  height: 38px;
  width: 90px;
}

.enabled-button:hover {
  cursor: pointer;
  transform: scale(1.02);
}

.enabled-button:active {
  transform: scale(0.97);
}

.disabled-button {
  background-color: #86dac5;
  border-radius: 4px;
  font-size: 20px;
  height: 38px;
  width: 90px;
}

.disabled-button:hover {
  cursor: not-allowed;
}

.wallet-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #6456f7;
  height: 65px;
}

@media (max-width: 1440px) {
  .wallet-header {
    height: 50px;
  }
}

.logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-container img {
  width: 40px;
  margin: 0 15px;
}

.user-email {
  font-size: 22px;
}

.info-container {
  display: flex;
  justify-content: space-around;
  font-size: 22px;
  width: 130px;
}

.currency-field {
  margin-right: 15px;
  margin-left: 7px;
}

.form-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  background-color: white;
  border: 1px solid black;
  border-radius: 6px;
  margin: auto;
}

.form-container h2 {
  font-size: 30px;
  margin: 10px auto;
}

.add-expenses-form {
  display: flex;
  flex-flow: column;
  align-items: center;
  font-size: 25px;
  margin: 25px auto;
}

.layer {
  margin: 20px 0;
}

.add-expenses-form input {
  border-left: white;
  border-right: white;
  border-top: white;
  outline: none;
  font-size: 17px;
  max-width: 60%;
  margin: 0 13px;
}

.add-expenses-form select {
  background: transparent;
  border: 2px black solid;
  border-radius: 6px;
  font-size: 21px;
  outline: none;
}

.second-row {
  display: flex;
  flex-flow: row;
  width: 70%;
}

.second-row input {
  text-align: center;
  width: 50%;
}

.add-expenses-button {
  background-color: #b1192d;
  color: white;
  font-size: larger;
  margin: 20px 0;
  padding: 15px;
}

.add-expenses-button:hover {
  cursor: pointer;
}

.report-container {
  border: 1px solid black;
  border-collapse: collapse;
  width: 65%;
  font-size: 17px;
  margin: 20px 25px;
  overflow: scroll;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th {
  border: 1px black solid;
  background-color: #16ad88;
  font-family: 'Recursive', sans-serif;
  width: 10%;
}

td {
  text-align: center;
  border-left: 1px black solid;
  border-right: 1px black solid;
  border-bottom: 1px black solid;
}

.value-column {
  background-color: #99d6c7;
}

::-webkit-scrollbar {
  width: 7px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  background: #6456f7;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: #4b3ee2;
}

.wallet-page-content {
  display: flex;
  height: 88vh;
  width: 80%;
  margin: 20px auto;
}


@media (min-width: 600px) and (max-width: 1440px) {
  .wallet-page-content {
    height: 84vh;
  }

  .form-container {
    margin: 20px;
  }

  .form-container h2 {
    font-size: 25px;
    margin: 10px auto;
  }

  .add-expenses-form {
    font-size: 19px;
    margin: 25px auto;
  }

  .second-row {
    width: 70%;
  }

  .second-row input {
    text-align: center;
    width: 50%;
    max-width: 50%;
  }

  .add-expenses-button {
    font-size: 18px;
    margin: 8px 0;
    padding: 8px;
  }
}

@media (max-width: 600px) {
  .wallet-page-content {
    flex-flow: column;
  }

  .form-container {
    margin: 10px auto;
    max-width: 80%;
  }

  .form-container h2 {
    font-size: 20px;
  }

  .add-expenses-form {
    font-size: 15px;
  }

  .layer {
    margin: 8px;
  }

  .add-expenses-form input {
    font-size: 15px;
  }

  .add-expenses-form select {
    border: 1px black solid;
    border-radius: 6px;
    font-size: 13px;
  }

  .add-expenses-button {
    font-size: 12px;
    margin: 10px 0;
    padding: 15px;
  }

  .report-container {
    width: 98%;
    margin: 10px auto;
    font-size: 15px;
    height: 50vh;
  }
}

